import { css } from 'emotion'
import { Color } from 'reactism/utils'
import { useEffect, useRef } from 'react'

const Contact = ({ positionX, setPositionX }) => {

  const wrapContacts = useRef(null)

  useEffect(() => {
    document.addEventListener('click', handleDocumentClick , false);
    return () => {
      document.removeEventListener('click', handleDocumentClick , false);
    }
  })

  const handleDocumentClick  = e => {
    if (!(wrapContacts.current).contains(e.target)) {
      setPositionX(true)
    }
  }

  const baseStyle = css`
    .overlayContact {
        visibility: ${ positionX ? `collapse` : `visible` };
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 153,51,0.8);
        z-index: 98;
      }
    .wrapContact {
      background-color: #fff;
      position: fixed;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 99;
      padding: 20px 30px;
      box-sizing: border-box;
      width: 400px;
      box-shadow: -1px 0 5px rgba(0,0,0,0.1);
      transition: 0.2s ease-in;
      transform: translateX(${ positionX ? `400px` : `0` });
    .closeContact {
      display: grid;
      justify-items: end;
      span {
        width: 22px;
        height: 22px;
        border-radius: 50%;
        border: 1.5px solid #d8d8d8;
        color: #d8d8d8;
        text-align: center;
        display: grid;
        align-items: center;
        font-size: 20px;
        cursor: pointer;
        transition: 0.2s;
        &:hover {
          border-color: ${Color.success};
          color: ${ Color.success }
        }
      }
    }
    .hr {
      border: 0.9px solid #d8d8d8;
    }
    .address {
      font-weight: 500;
      font-size: 16px;
      > div {
        display: grid;
        grid-template-columns: 1fr 6fr;
        align-items: center;
      }
      img {
        width: 30px;
      }
    }
    .wrapForm {
      display: grid;
      grid-template-columns: 1fr;
      grid-row-gap: 10px;
      span {
        color: red;
      }
      input, textarea {
        outline: none;
        border: 1.5px solid #d8d8d8;
        border-radius: 5px;
        padding: 10px 5px;
        transition: 0.3s;
        &:focus {
          border-color: ${ Color.success };
          box-shadow: 0 1px 5px #d8d8d8;
        }
      }
    }
  }
`

  return(
    <div className={ baseStyle }>
      <div className="overlayContact" />
      <div className="wrapContact" ref={ wrapContacts }>
        <div className="closeContact" onClick= { () => setPositionX(400) }>
          <span>&times;</span>
        </div>
        <div className="address">
          <div>
            <img src="/static/icon/ic_lokasi.svg"/>
            <p>JL Sultan Hassanudin No.43, Coppo, Kec. Barru, Kab. Barru, Sulsel 90712</p>
          </div>
          <div>
            <img src="/static/icon/ic_telepon.svg"/>
            <p>(0427) 21057</p>
          </div>
          <div>
            <img src="/static/icon/ic_email.svg"/>
            <p>kejari_barru@mail.com</p>
          </div>
        </div>
        <div className="hr" />
        <form>
          <div className="wrapForm">
            <h2>Pesan Untuk Kami</h2>
            <label>Nama Lengkap <span>*</span></label>
            <input type="text"/>
            <label>Email / Telepon <span>*</span></label>
            <input type="text"/>
            <label>Pesan Anda <span>*</span></label>
            <textarea cols="30" rows="3"></textarea>
          </div>
        </form>
      </div>
    </div>
  )
}

export default Contact